import { Col, Divider, Row, Spin, Tag } from "antd";
import Title from "antd/lib/typography/Title";
import React, { useEffect, useState } from "react";
import { useNavigate, useParams, Navigate } from "react-router-dom";
import { GAMES } from "../../constants/crypto";
import PropTypes from "prop-types";
import { TokenCard, TokenCol } from "./Game.style";
import { formatCurrency, getNonP2ETokens, updateTitle } from "../../helpers";
import { TextPercentage } from "../../components/TokenTable/TokenTable.style";
import Discord from "../../components/Discord/Discord";

export const Game = () => {
  let { id } = useParams();

  const navigate = useNavigate();
  const game = GAMES.find((g) => g.abbr == id);

  // console.log("GAME", game);
  if (!game) {
    navigate("/");
    return <Navigate to="/" />;
  }

  updateTitle("GAME: " + game.name);
  const { links = [], tokens = [], discord = [] } = game;
  return (
    <div className="text-start">
      <Title className="d-flex align-items-center">
        <img width={38} style={{ marginRight: 10 }} src={game.icon} />
        {game.name}
      </Title>

      <RenderLinks links={links} />
      <Divider />
      <RenderTokens tokens={tokens} />
      <Divider orientation="left">
        <Title level={3}>Discord</Title>
      </Divider>
      {discord.map((d) => (
        <Discord key={d[0]} name={d[0]} id={d[1]} />
      ))}
    </div>
  );
};

const RenderLinks = ({ links = [] }) => {
  return (
    <Row>
      <Col>
        <p>
          {links.map((l, i) => (
            <Tag style={{ marginBottom: 3 }} key={i}>
              <a
                target="_blank"
                style={{ textDecoration: "none" }}
                href={l[1]}
                rel="noreferrer"
              >
                {l[0]}
              </a>
            </Tag>
          ))}
        </p>
      </Col>
    </Row>
  );
};
RenderLinks.propTypes = {
  links: PropTypes.array,
};
const RenderTokens = ({ tokens = [] }) => {
  const [loading, setLoading] = useState(true);
  const [_tokens, setTokens] = useState([]);
  const initGame = async () => {
    const tkns = await getNonP2ETokens(tokens);
    setTokens(tkns);
    setLoading(false);
  };
  useEffect(() => {
    initGame();
  }, []);
  return (
    <Row gutter={16}>
      <Spin spinning={loading}></Spin>
      {_tokens.map((t, i) => (
        <TokenCol key={i}>
          <TokenCard size="small">
            <div
              style={{ minHeight: 48 }}
              className="d-flex flex-row align-items-center"
            >
              <img style={{ marginRight: 10 }} width={35} src={t.icon} />
              <div>
                <div className="fw-bold">
                  {t.name} ({t.abbr})
                </div>
                <div>
                  {formatCurrency(t.php)}
                  <span style={{ marginLeft: 3 }} className="text-secondary">
                    (${t.usd})
                  </span>
                </div>
                <div>
                  <TextPercentage positive={t.php_24h_change > 0}>
                    {t.php_24h_change > 0 && "+"}
                    {t.php_24h_change.toFixed(2)}%
                  </TextPercentage>
                </div>
              </div>
            </div>
          </TokenCard>
        </TokenCol>
      ))}
    </Row>
  );
};
RenderTokens.propTypes = {
  tokens: PropTypes.array,
};
export default Game;
